"use client";
import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiCar, FiTool, FiMessageSquare, FiSend, FiX } from 'react-icons/fi';

export default function ServiceRequestPage() {
  const [activeTab, setActiveTab] = useState<'service' | 'field' | 'comment'>('service');

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-gray-800 p-4 md:p-8">
      <motion.div 
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
        className="max-w-4xl mx-auto"
      >
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <motion.h1 
            initial={{ x: -50 }}
            animate={{ x: 0 }}
            className="text-3xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-cyan-400 to-blue-500"
          >
            TMS Service Portal
          </motion.h1>
        </div>

        {/* Tab Navigation */}
        <motion.div className="flex mb-8 border-b border-gray-700">
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setActiveTab('service')}
            className={`px-6 py-3 flex items-center ${activeTab === 'service' ? 'text-cyan-400 border-b-2 border-cyan-400' : 'text-gray-400'}`}
          >
            <FiCar className="mr-2" /> Car Service
          </motion.button>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setActiveTab('field')}
            className={`px-6 py-3 flex items-center ${activeTab === 'field' ? 'text-cyan-400 border-b-2 border-cyan-400' : 'text-gray-400'}`}
          >
            <FiTool className="mr-2" /> Field Request
          </motion.button>
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={() => setActiveTab('comment')}
            className={`px-6 py-3 flex items-center ${activeTab === 'comment' ? 'text-cyan-400 border-b-2 border-cyan-400' : 'text-gray-400'}`}
          >
            <FiMessageSquare className="mr-2" /> Send Comment
          </motion.button>
        </motion.div>

        {/* Tab Content */}
        <AnimatePresence mode="wait">
          <motion.div
            key={activeTab}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -20 }}
            transition={{ duration: 0.3 }}
          >
            {activeTab === 'service' && <CarServiceForm />}
            {activeTab === 'field' && <FieldRequestForm />}
            {activeTab === 'comment' && <CommentForm />}
          </motion.div>
        </AnimatePresence>
      </motion.div>
    </div>
  );
}